@import url("https://fonts.googleapis.com/css2?family=Agbalumo&family=Jua&family=Noto+Sans+KR:wght@400;600;700&display=swap");

@font-face {
  font-family: "Binggrae-Two";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_one@1.0/Binggrae-Bold.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

:root {
  --mygray: #6c757d;
  --myblack: #343a40;
  --lgtpurple: #eae9fe;
  --lgmidpurple: #c9c1ff;
  --lmidpurple: #b1a5ff;
  --midpurple: #998aff;
  --mstrpurple: #6a53ff;
  --strpurple: #5c37eb;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f5f3f4;
  font-family: "Noto Sans KR", sans-serif;
  padding-top: 50px;
}

/* Components */

/* button */
.button {
  width: 250px;
  height: 50px;
  background-color: var(--midpurple);
  border-radius: 5px;
  border: 0px;
  border-style: solid;
  margin-bottom: 15px;
  color: white;
  font-weight: 600;
  font-family: "Noto Sans KR", sans-serif;
}

.button:hover {
  background-color: var(--strpurple);
}
/* 
MyHeader
*/
.MyHeader {
  position: fixed;
  top: 0px;
  left: 0;
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 5px;
  box-shadow: 0 1px 4px -2px #989ca0;
  background-color: #f5f3f4;
  @media (max-width: 768px) {
    width: 100vw;
  }
}

/* Login 

page */
.login_box div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.login_box div input {
  width: 300px;
  height: 40px;
  margin-bottom: 15px;
  border: none;
  border-radius: 3px;
  font-family: "Noto Sans KR", sans-serif;
}

.button_box {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
}

.button_up_text {
  font-size: 12px;
  margin-bottom: 5px;
}

/* Join

page */
.join_box_element input {
  width: 300px;
  height: 40px;
  margin-bottom: 15px;
  border: none;
  border-radius: 3px;
}

.join_box_element select {
  width: 150px;
  height: 30px;
  border: none;
  border-radius: 3px;
  margin-bottom: 15px;
  font-family: "Noto Sans KR", sans-serif;
  text-align-last: center;
  text-align: center;
}

.join_box_text {
  margin-bottom: 6px;
  color: var(--myblack);
  padding-left: 5px;
}

.join_box_element button {
  margin-top: 20px;
  width: 305px;
}

/* Survey

*/
.survey_qs {
  width: 600px;
  background-color: white;
  padding: 20px 30px 15px 30px;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.1);
}

.survey_q_box {
  margin-bottom: 30px;
  justify-content: center;
}

.survey_q_text {
  margin-bottom: 20px;
}
.checkbox_list_box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.checkbox_list_box_feedback {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.checkbox_list_box_feedback > img {
  width: 300px;
}

.checkbox_items {
  width: 120px;
  height: 40px;
  margin: 10px 40px;
  justify-content: space-between;
  border: 1.3px var(--mygray);
  border-style: solid;
  background-color: var(--lgtpurple);
  position: relative;
  border-radius: 30px;
  box-shadow: 1px 1px 1px var(--mygray);
}

.checkbox_items:last-child {
  margin-right: 240px;
}

.checkbox_items_feedback {
  width: 160px;
  height: 40px;
  margin: 40px 20px 10px 20px;
  justify-content: space-between;
  border: 1.3px var(--mygray);
  border-style: solid;
  background-color: var(--lgtpurple);
  position: relative;
  border-radius: 30px;
  box-shadow: 1px 1px 1px var(--mygray);
}

.checkbox_items_box_feedback {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.checkbox_item_checked {
  background-color: var(--midpurple);
}

.checkbox_items input {
  position: absolute;
  left: 30px;
  top: 10px;
}

.checkbox_items_feedback input {
  position: absolute;
  left: 10px;
  top: 10px;
}

.checkbox_items label {
  position: absolute;
  left: 54px;
  top: 7px;
}
.checkbox_items_feedback label {
  position: absolute;
  left: 32px;
  top: 7px;
}

div.checkbox_items:has(input.checkbox_item_checked) {
  background-color: var(--lgmidpurple);
}
div.checkbox_items_feedback:has(input.checkbox_item_checked) {
  background-color: var(--lgmidpurple);
}
.button_survey {
  background-color: #d8e1ff;
  color: var(--myblack);
  /* border: 0.5px var(--mygray);
  border-style: solid; */
  margin-left: 250px;
  width: 100px;
  font-weight: 600;
}
.button_survey:hover {
  background-color: #bbd0ff;
}

/* Survey goalmoney

page
*/
.goalmoney_box_element {
  position: relative;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
  padding: 10px;
  padding-left: 35px;
  background-color: var(--lgtpurple);
}
.goalmoney_box_element input {
  position: absolute;
  right: 45px;
  top: 15px;
  width: 175px;
  height: 35px;
  margin-bottom: 15px;
  font-family: "Noto Sans KR", sans-serif;
  text-align: center;
  border: 0;
  border-bottom: 3px solid;
  border-color: #c4bddb;
  border-radius: 2px;
  font-size: 25px;
  font-weight: 700;
  background-color: (var(--lgtpurple));
  color: #726892;
}
.goalmoney_box_element input:focus {
  outline: none;
  border-bottom: 3px solid;
  border-color: var(--mygray);
  color: var(--mygray);
}

/* Mission

*/
.Mission .MissionList {
  width: 800px;
  height: 800px;
  @media (max-width: 768px) {
    width: 100vw;
  }
}
.mission_select_box .SelectMenu {
  width: 150px;
  height: 30px;
  border: 1px var(--mygray) solid;
  border-radius: 3px;
  margin-bottom: 15px;
  font-family: "Noto Sans KR", sans-serif;
  text-align-last: center;
  text-align: center;
  @media (max-width: 768px) {
    width: 20vw;
    font-size: 10px;
    margin-left: 10px;
    margin-bottom: 5px;
  }
}

/* feedback */
.feedback_page {
  display: flex;
  position: absolute;
  top: 20%;
  left: 17%;
  @media (max-width: 768px) {
    width: 95vw;
    height: 90vh;
    flex-direction: column;
    left: 10px;
    top: 50px;
  }
}

.feedback_img {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  @media (max-width: 768px) {
    position: relative;
    flex-direction: row;
  }
}

.feedback_img:first-child {
  bottom: -95px;
  left: -10px;
  width: 250px;
  @media (max-width: 768px) {
    top: 0px;
    left: 0px;
  }
}

@media (max-width: 768px) {
  .feedback_img:first-child div:first-child {
    display: none;
  }
}

@media (min-width: 768px) {
  .feedback_img:first-child div:last-child {
    display: none;
  }
}
.feedback_img:last-child {
  bottom: -110px;
  left: 10px;
  width: 250px;
  @media (max-width: 768px) {
    top: 0px;
    left: 30vw;
  }
}

.feedback_img img {
  height: 300px;
  margin-left: 0px 20px;
  @media (max-width: 768px) {
    height: 100px;
  }
}

.feedback_img:last-child > img {
  height: 295px;
  @media (max-width: 768px) {
    height: 90px;
  }
}

.feedback_img div {
  padding: 15px 15px;
  margin: 15px 15px;
  background-color: var(--lgtpurple);
  width: 170px;
  font-size: 18px;
  font-weight: lighter;
  border-radius: 20px;
  font-family: "Jua", sans-serif;
  position: relative;
  @media (max-width: 768px) {
    font-size: 11px;
  }
}

.feedback_img:last-child div {
  width: 160px;
}

.feedback_img:first-child div::before {
  position: absolute;
  display: block;
  top: 95px;
  content: "▼";
  font-size: 1.3rem;
  left: 100px;
  color: var(--lgtpurple);
}

.feedback_img:last-child div::after {
  position: absolute;
  display: block;
  top: 70px;
  content: "▼";
  font-size: 1.3rem;
  left: 100px;
  color: var(--lgtpurple);
}

@media (max-width: 768px) {
  .feedback_img:last-child div::after {
    content: "▶";
    top: 15px;
    left: 140px;
  }
  .feedback_img:first-child div::before {
    content: "◀";
    left: -15px;
    top: 20px;
  }
}

.FeedbackList {
  background-color: white;
  padding: 20px 30px;
  width: 550px;
  height: 65vh;
  overflow: auto;
  @media (max-width: 768px) {
    width: 80vw;
  }
}

.feedback_button {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* vault */
.VaultList {
  width: 700px;
  height: 800px;
  @media (max-width: 768px) {
    width: 100vw;
  }
}

.vault_buttons {
  margin-top: 7px;
  position: absolute;
  display: flex;
  flex-direction: column;
  right: 0px;
  top: 0px;
}

/* profile */
.profile_page {
  width: 800px;
  height: 600px;
}
.profile_info {
  display: flex;
}

.profile_img {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile_img > div:nth-child(2) {
  font-weight: 700;
}

.profile_img img {
  height: 325px;
}

.profile_img div {
  font-size: 18px;
}

.profile_infos {
  width: 450px;
  margin-left: 70px;
  margin-top: 30px;
}

.profile_infos > div {
  letter-spacing: 1px;
  border-radius: 12px;
}

.profile_infos > div:first-child {
  font-size: 25px;
  margin-bottom: 10px;
}
.profile_infos > div:nth-child(2) {
  font-size: 18px;
  margin-top: 30px;
  margin-left: 0px;
  background-color: white;
  border: 1px #cccccc;
  border-style: solid;
  box-shadow: 1px 1px 1px #cccccc;
  padding: 10px 20px;
}
.profile_infos > div:nth-child(2) > div:nth-child(1) {
  display: flex;
  justify-content: center;
  margin-bottom: 5px;
  font-size: 20px;
}
.profile_infos > div:nth-child(2) > div:nth-child(2) {
  display: flex;
  justify-content: space-between;
}

.profile_infos > div:nth-child(2) > div > div {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 35px;
  margin: 10px 15px;
  border: 1.3px var(--mygray);
  border-style: solid;
  background-color: var(--lgtpurple);
  position: relative;
  border-radius: 30px;
  box-shadow: 1px 1px 1px var(--mygray);
}

.profile_infos > div:nth-child(3) {
  margin: 15px 0px;
  height: 50px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1.3px #cccccc;
  border-style: solid;
  box-shadow: 1px 1px 1px #cccccc;
  font-size: 19px;
}
.profile_infos > div:nth-child(3) > div {
  font-size: 22px;
  font-weight: 600;
  margin-left: 5px;
  margin-right: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile_infos > div:nth-child(4) {
  margin: 10px 0px;
  height: 50px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1.3px #cccccc;
  border-style: solid;
  box-shadow: 1px 1px 1px #cccccc;
  font-size: 19px;
}

.profile_infos > div:nth-child(4) > div {
  font-size: 22px;
  font-weight: 600;
  margin-left: 5px;
  margin-right: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile_mission {
  margin-top: 30px;
  width: 800px;
  position: absolute;
}

.profile_mission > div:first-child {
  background-color: var(--lgtpurple);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60px;
  font-size: 20px;
  font-weight: 600;
  border-radius: 10px;
}

.profile_mission > div:first-child > div {
  margin-left: 5px;
  margin-right: 1px;
  font-size: 28px;
  font-weight: 600;
  position: relative;
  top: -2px;
}

.profile_mission > div:nth-child(2) > div:first-child {
  margin-top: 12px;
  margin-bottom: 12px;
  font-size: 16px;
  margin-left: 5px;
}

/* 최근 미션 3개 */
.profile_mission > div:nth-child(2) > div:nth-child(2) {
  display: flex;
  justify-content: space-between;
}

.profile_mission > div:nth-child(2) > div:nth-child(2) > div {
  width: 30%;
  height: 55px;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #cfdefd;
  font-weight: 600;
  border-radius: 10px;
  box-shadow: 2px 2px 2px var(--mygray);
}

.home_all {
  display: flex;
  align-items: center;
  @media (max-width: 768px) {
    flex-direction: column;
  }
}

/* 랭킹 */

.RankingTopList {
  width: 75vw;
  margin-top: 20px;
}

.RankingFriendList {
  width: 75vw;
}

.RankingTopList > div:first-child {
  font-size: 23px;
}

.RankingFriendList > div:first-child {
  font-size: 23px;
}

.RankingFriendList > div:nth-child(2) {
  font-size: 20px;
  margin-left: 5px;
}
.rank_weekly_box {
  display: flex;
}

.rank_friend_box {
  display: flex;
  flex-wrap: wrap;
}

.RankingFriendItem {
  width: 18%;
  margin: 20px 1%;
}

/* statistics */
.statistics_page {
  height: 95vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.statistics_title {
  font-size: 23px;
  margin-bottom: 10px;
}

.statistics_graph {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 700px;
}

.statistics_graphs {
  font-size: 20px;
  display: flex;
  width: 1400px;
}
.statistics_text {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media (max-width: 768px) {
  .swal2-wide {
    position: absolute;
    left: 10vw;
    top: -200px;
  }
}

.feedback_alert_box {
  display: flex;
}
.feedback-alert {
  width: 430px;
  position: relative;
  @media (max-width: 768px) {
    width: 350px;
    font-size: 13px;
    position: absolute;
    left: 10px;
    top: 50px;
  }
}
.feedback-alert img {
  width: 80px;
  height: 60px;
  @media (max-width: 768px) {
    width: 70px;
    height: 50px;
  }
}

.text-alert {
  width: auto;
}

.feedback-alert div {
  margin-top: 3px;
}
